import {useRecoilValue, useSetRecoilState} from 'recoil';
import {emoticonsAtom, emoticonsSelector} from '../../atoms/atom';
import {EQueryKey} from '../../app.constant';
import MainBanner from '../../components/MainBanner/MainBanner';
import styled from 'styled-components';
import {useQuery} from 'react-query';
import {getEmoticon} from '../../api';

const SLayout = styled.div`

`

export default function New() {
    const emoticons = useRecoilValue(emoticonsSelector(1));
    const setEmoticons = useSetRecoilState(emoticonsAtom);
    const { isLoading} = useQuery([EQueryKey.EMOTICONS], getEmoticon,{
        // refetchOnWindowFocus: false,
        onSuccess: (response) => {
            setEmoticons(response.data)
        }
    });
    return <>
        <SLayout>
            <MainBanner type={'newMainBanner'}>
                <video autoPlay={true} muted={true} loop={true} playsInline={true} >
                    <source src="//update.coolmessenger.com/_ImageServer/jirancomms/img/서비스흐름.mp4" type="video/mp4" />
                </video>
            </MainBanner>
            {isLoading ? <div>Loading...</div> :
                <>
                    <section>
                        {emoticons.map((d: any) => <article key={d.name}>
                                <h3>{d.name}</h3>
                            {d.data[0].image}
                            </article>)}
                    </section>
                </>
            }
        </SLayout>
    </>
}
